.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

nav {
  display: flex;
  align-items: center;
  background-color: #ffffff;
  padding: 10px 25px;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
  z-index: 1;
}

.nav--logo_icon {
  width: 80px;
  margin-right: 10px;
}

.nav--logo_text {
  color: salmon;
  text-shadow: 0 0 5px lime;
  margin-right: auto;
  font-size: 30px;
}

.nav--title {
  color: #DEEBF8;
}

.nav--link {
  text-decoration: none;
}

.connectWalletBtn {
  width: 200px;
  height: 40px;
  font-size: 20px;
  font-weight: bold;
  background-color: salmon;
  border-radius: 10px;
  cursor: pointer;
}

.connectWalletBtn:hover {
  background-color: teal;
  color: antiquewhite;
}

.connectWalletBtn:active {
  color: yellowgreen;
}